.filter__title {
  color: #63D1BB;
}
.filter__label {
  font-size: 17px;
  line-height: 18px;
  color: #000000;
}
.filter__input-wrapper {
  display: flex;
  flex-direction: column;
  border-bottom:1px solid #000000 ;
}
.filter__input{
  border: none;
}
.filter__input::placeholder {
  font-family: "Open-sans";
  font-size: 17px;
  line-height: 18px;
  opacity: 0.3;
}
// одинаковая стилизация обьединить
.filter__area {
  font-family: "Open-sans";
  font-size: 17px;
  line-height: 18px;
  opacity: 0.3;
}
.filter__text-necessarily {
  font-family: "Open-sans";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #444444;
}

